import * as React from 'react';
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import axios from 'axios';

export default function Row({item, user, base_url}) 
{
    const [open, setOpen] = React.useState(false);

    function getStatus(item)
    {
        if(!item.is_ok && !item.is_cancel) return 'В ожидании'
          else if(item.is_ok) return 'Выполнено'
        else return 'Отклонено'
    }
    const declined = async() => {const response = axios.put(`${base_url}api/history/`, {id:item.id})}
    const confirmed = async() => {const response = axios.patch(`${base_url}api/history/`, {id:item.id})}
    return (
      <React.Fragment>
        <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
          <TableCell>
            <IconButton
              aria-label="expand row"
              size="small"
              onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUpIcon id='arrow' /> : <KeyboardArrowDownIcon id='arrow'/>}
            </IconButton>
          </TableCell>
          <TableCell id='table-items' component="th" scope="row">Продажа</TableCell>
          <TableCell id='table-items' align="left">{new Date(item?.date_time).toLocaleDateString() + ', ' + new Date(item?.date_time).toLocaleTimeString()}</TableCell>
          <TableCell id='table-items' align="right">{item.total_price}</TableCell>
          <TableCell id='table-items' align="right">{item.wallet}</TableCell>
          <TableCell id='table-items' align="right">{getStatus(item)}</TableCell>
          {user.is_admin && <TableCell id='table-items' align="right"><button onClick={confirmed}>Подтвердить</button> <button onClick={declined}>Отменить</button></TableCell>}
        </TableRow>
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={7}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box sx={{ margin: 1 }}>
                <Typography id='table-items' variant="h6" gutterBottom component="div">Наименование объекта</Typography>
                <Table size="small" aria-label="purchases">
                  <TableBody>
                    {item.object_name.map((name) => (
                      <TableRow key={name}>
                        <TableCell id='table-items' component="th" scope="row">{name}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </Box>
            </Collapse>
          </TableCell>
        </TableRow>
        
      </React.Fragment>
    );
  }